@import url("https://fonts.googleapis.com/css?family=Faster+One&display=swap");

html,
body,
#root {
  height: 100%;
  padding: 0;
  background-color: #833ab4;
  /* background: linear-gradient(to right bottom, #405de6, #fcaf45); */
}

.app {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.app-board {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.app-board-left {
  display: flex;
  flex-direction: column;
  padding-right: 50px;
}

.title {
  font-family: "Faster One", cursive;
  /* font-family: "lucida grande", tahoma, verdana, arial, sans-serif; */
  font-size: 6vw;
  white-space: nowrap;
  color: rgb(255, 255, 255);
}

.info {
  /* border: 5px inset; */
  border: 5px inset #fff;
  border-radius: 3px;
  height: 100%;
  background-color: rgb(248, 241, 241);
  /*  border-width: 2px;
  border-style: inset; */
}

.app-spectrum {
  display: flex;
}

.spectrum {
  background-color: rgb(255, 255, 255);

  font-size: 30px;
  width: 100%;
  height: 100%;
  margin-top: 20px;
}

.game {
  display: flex;
  flex-direction: row;
  padding-top: 70px;

  /* background-color: black; */
}
.game-grid {
  display: flex;
  flex-direction: column;
  border: 6px double #fcaf45;
}

.game-pieces {
  display: flex;
  flex-direction: column;
}

.line {
  display: flex;
  flex-direction: row;
}

.box {
  width: 20px;
  height: 20px;
  /* background-color: aquamarine; */
  background-color: #fff;
  border: 4px double #fcaf45;
}

